import {Component, OnInit} from '@angular/core';
import {Router,NavigationExtras } from '@angular/router';
import {Platform, ToastController} from '@ionic/angular';
import {TokenStorageService} from '../../../_servicesAuth/token-storage.service';
import { Observable, from } from 'rxjs';
import { delay } from 'rxjs/operators';
@Component({
  selector: 'app-profil',
  templateUrl: './profil.component.html',
  styleUrls: ['./profil.component.scss'],
})
export class ProfilComponent implements OnInit {
  urlClient = 'https://smarteasyorders.com/client/listing?canal=Online';
  urlPreparCommand = ' https://smarteasyorders.com/command/listing';
  currentUser: any;
  isMobile: boolean;
  constructor(
    private router: Router,
    private toastController: ToastController,
    private token: TokenStorageService,
    private platform: Platform,
  ) {
    this.isMobile = this.platform.is('mobile');
  }

  ngOnInit(): void {
    this.currentUser = this.token.getUser();
  }

  goToClientPage() {
    const navigationExtras: NavigationExtras = {
      queryParams: {canal: 'Online'}
    };
    this.router.navigate(['/client'], navigationExtras);
    //this.router.navigate(['/client/listing?canal=Online']);
  }

  goToPrepareCommand() {
    this.router.navigate(['/command']);
  }

  async presentToast(message: string) {
    const toast = await this.toastController.create({
      message,
      duration: 2000, // 4000 ms (4 secondes)
      position: 'middle', // Position du toast (peut être 'top', 'middle' ou 'bottom' ou 'top')
      color: 'success',
      cssClass: 'ion-text-center'
    });
    toast.present();
  }

  copyUrLClient() {
    const textArea = document.createElement('textarea');
    textArea.value = this.urlClient;
    document.body.appendChild(textArea);
    textArea.select();
    document.execCommand('copy');
    document.body.removeChild(textArea);
    console.log('Texte copié :', this.urlClient);
    this.presentToast('Copier');
  }

  copyUrLPreparCommand() {
    const textArea = document.createElement('textarea');
    textArea.value = this.urlPreparCommand;
    document.body.appendChild(textArea);
    textArea.select();
    document.execCommand('copy');
    document.body.removeChild(textArea);
    console.log('Texte copié :', this.urlPreparCommand);
    this.presentToast('Copier');
  }

  /*logout(): void {
    this.token.signOut();
    window.location.reload();
    this.router.navigate(['/']);
  }*/

  logout() {
    // Simulate an async operation (e.g., API call) with a delay using RxJS
    const asyncOperation: Observable<any> = from(new Promise(resolve => {
      setTimeout(() => {
        // After the delay, reload the page
        this.token.signOut();
        window.location.reload();

        // After the reload, navigate to the home page
        this.router.navigate(['/']);

        resolve(); // Complete the async operation
      }, 1000); // Simulated delay of 1 second
    }));
  }
}
